export default class SearchForm {
  constructor(el) {
    this.el = el

    this.setVariables()
    this.addListeners()
  }

  /*
   * variables
   */

  setVariables() {
    this.controls = this.el.querySelectorAll(
      'input, select, button[type="submit"]'
    )

    this.resets = this.el.querySelectorAll(
      'input[type="reset"], button[type="reset"]'
    )
  }

  /*
   * events
   */

  addListeners() {
    for (let i = 0, len = this.controls.length; i < len; i++) {
      this.controls[i].addEventListener('change', this.submitForm.bind(this))
    }

    for (let r = 0, len = this.resets.length; r < len; r++) {
      this.resets[r].addEventListener('click', this.resetForm.bind(this))
    }
  }

  submitForm() {
    this.el.submit()
  }

  resetForm(event) {
    event.preventDefault()

    let checkedBoxes = this.el.querySelectorAll(
      'input[type="checkbox"]:checked'
    )

    for (let i = 0, len = checkedBoxes.length; i < len; i++) {
      checkedBoxes[i].checked = false
    }

    this.submitForm()
  }
}



